import React, { useState, useEffect } from "react";
import { Col, message, Row, Select, DatePicker, Button } from "antd";
import { ClockCircleOutlined, CloseCircleOutlined } from "@ant-design/icons";
import AsyncSelect from "react-select/async";

import Auxiliary from "util/Auxiliary";
import CCU from "./components/CCU";
import Traffic from "./components/Traffic";

import axios from "util/Api";
import {
  getDomainList,
  searchEmail,
} from "../../../appRedux/actions/Dashboard";
import { getReportData } from "../../../appRedux/actions/Report";
import { connect } from "react-redux";
import { isEmpty } from "util/formatData";
import Cookies from "universal-cookie";
import moment from "moment";

const cookies = new Cookies();
const roleAccount = cookies.get("role");
const userEmail = cookies.get("email");
const { Option, OptGroup } = Select;
const { RangePicker } = DatePicker;

const mapStateToProps = () => ({});
const Dashboard = (props) => {
  const [selectedUser, setSelectedUser] = useState({});
  const [selectedDomain, setSelectedDomain] = useState({});

  const [defaultUserList, setDefaultUserList] = useState([]);
  const [defaultDomainList, setDefaultDomainList] = useState([]);

  const [field, setField] = useState("all");
  const [start, setStart] = useState(
    moment().subtract(30, "minutes").utc().toISOString()
  );
  const [end, setEnd] = useState(moment().utc().toISOString());
  const [gran, setGran] = useState(30);
  const [isDatePicker, setIsDatePicker] = useState(false);
  const [timePicker, setTimePicker] = useState("30m");

  const [CCUData, setCCUData] = useState([]);
  const [P2PData, setP2PData] = useState([]);
  const [HttpData, setHttpData] = useState([]);

  //#region  Xu ly select & search user

  // handle selection
  const handleChange = (value) => {
    setSelectedUser(value);
  };

  const handleDomainChange = (value) => {
    setSelectedDomain(value);
  };

  const loadUserOptions = (inputValue) => {
    return axios
      .get(`admin/email/search?page=1&page_size=5&keyword=${inputValue}`)
      .then((res) => res.data.data);
  };

  const loadDomainOptions = (inputValue) => {
    var url = ``;
    var email;
    if (roleAccount === "super" || roleAccount === "admin") {
      email = isEmpty(selectedUser) ? "" : selectedUser.value;
    } else email = userEmail;

    if (isEmpty(selectedUser)) {
      url = `domain/search?page=1&page_size=10&keyword=${inputValue}`;
    } else
      url = `domain/search?email=${email}&page=1&page_size=10&keyword=${inputValue}`;
    return axios.get(url).then((res) => res.data.data.data);
  };

  const handleSelectTimeRange = (value) => {
    var startData;
    switch (value) {
      case "datepicker":
        setIsDatePicker(true);
      case "30m":
        setGran(30);
        startData = moment().subtract(30, "minutes").utc().toISOString();
        setStart(startData);
        break;
      case "1h":
        setGran(30);
        startData = moment().subtract(60, "minutes").utc().toISOString();
        setStart(startData);
        break;
      case "6h":
        setGran(60);
        startData = moment().subtract(6, "hours").utc().toISOString();
        setStart(startData);
        break;
      case "12h":
        setGran(60 * 5);
        startData = moment().subtract(12, "hours").utc().toISOString();
        setStart(startData);
        break;
      case "24h":
        setGran(60 * 10);
        startData = moment().subtract(24, "hours").utc().toISOString();
        setStart(startData);
        break;
      case "7d":
        setGran(60 * 60);
        startData = moment().subtract(7, "days").utc().toISOString();
        setStart(startData);
        break;
      case "30d":
        setGran(60 * 60 * 6);
        startData = moment().subtract(30, "days").utc().toISOString();
        setStart(startData);
        break;
      default:
        setGran(30);
        startData = moment().subtract(30, "minutes").utc().toISOString();
        setStart(startData);
    }
  };

  const onFieldChange = (value) => {
    setField(value);
  };

  const handleSubmitDatePicker = (dates) => {
    if (dates && dates[0] && dates[1]) {
      var string =
        dates[0].format("YYYY/MM/DD HH:mm:ss") +
        " - " +
        dates[1].format("YYYY/MM/DD HH:mm:ss");
      var diffInMinutes = dates[1].diff(dates[0], "minutes");
      let granPicker = diffInMinutes * 1000;
      setGran(granPicker);

      setStart(dates[0].utc().toISOString());
      setEnd(dates[1].utc().toISOString());

      setTimePicker(string);
    }
    // console.log("From: ", dateStrings[0], ", to: ", dateStrings[1]);
    setIsDatePicker(false);
  };

  const handleCancle = () => {
    setIsDatePicker(false);
  };

  const getCCUData = () => {
    props.getReportData(
      {
        domain: isEmpty(selectedDomain)
          ? cookies.get("selectedDomain")
          : selectedDomain.domain,
        field: "num_rt",
        start: start,
        end: end,
        gran: gran,
        other_field: "",
      },
      (code, res) => {
        if (code === 200) {
          
          if (res.data !== null) {
            setCCUData(res.data);
          } else {
            setCCUData([]);
          }
        } else if (code === 1007) {
        } else message.error("Error when getting report data!");
      }
    );
  };

  const getHttpData = () => {
    props.getReportData(
      {
        domain: isEmpty(selectedDomain)
          ? cookies.get("selectedDomain")
          : selectedDomain.domain,
        field: "traffic_http_day",
        start: start,
        end: end,
        gran: gran,
        other_field: "",
      },
      (code, res) => {
        if (code === 200) {
          
          if (res.data !== null) {
            setHttpData(res.data);
          } else {
            setHttpData([]);
          }
        } else if (code === 1007) {
        } else message.error("Error when getting report data!");
      }
    );
  };

  const getP2PData = () => {
    props.getReportData(
      {
        domain: isEmpty(selectedDomain)
          ? cookies.get("selectedDomain")
          : selectedDomain.domain,
        field: "traffic_p2p_day",
        start: start,
        end: end,
        gran: gran,
        end: end,
        other_field: "",
      },
      (code, res) => {
        if (code === 200) {
          
          if (res.data !== null) {
            setP2PData(res.data);
          } else {
            setP2PData([]);
          }
        } else if (code === 1007) {
        } else message.error("Error when getting report data!");
      }
    );
  };

  useEffect(() => {
    if (roleAccount === "admin" || roleAccount === "super") {
      props.searchEmail(
        { page: 1, page_size: 20, keyword: "" },
        (code, res) => {
          if (code === 200) {
            setDefaultUserList(res);
          } else message.error("Error when fetching user list !");
        }
      );
    }
  }, []);

  useEffect(() => {
    var email;
    if (roleAccount === "super" || roleAccount === "admin") {
      email = isEmpty(selectedUser) ? "" : selectedUser.value;
    } else email = userEmail;
    props.getDomainList(
      {
        page: 1,
        page_size: 20,
        keyword: "",
        email: email,
      },
      (code, res) => {
        if (code === 200) {
          setDefaultDomainList(res.data);
        } else message.error("Error when fetching domain list !");
      }
    );
  }, [selectedUser]);

  useEffect(() => {
    getCCUData();
    cookies.set(
      "selectedDomain",
      isEmpty(selectedDomain) ? "" : selectedDomain.domain
    );
  }, [selectedDomain, start, end]);

  useEffect(() => {
    if (field === "all") {
      getHttpData();
      getP2PData();
    } else if (field === "http") {
      getHttpData();
    } else {
      getP2PData();
    }
  }, [selectedDomain, start, end, field]);

  //#endregion

  const colourStyles = {
    control: (styles) => ({
      ...styles,
      border: "none",
      borderRadius: "8px",
      boxShadow: "0 0 5px 5px rgba(0, 0, 0, 0.03)",
    }),
    menu: (styles) => ({
      ...styles,
      border: "none",
      borderRadius: "8px",
      boxShadow: "0 0 5px 5px rgba(0, 0, 0, 0.03)",
    }),
  };

  return (
    <Auxiliary>
      <Row>
        {roleAccount === "admin" || roleAccount === "super" ? (
          <Col xxl={6} xl={5} lg={12} md={12} sm={12} xs={12}>
            <AsyncSelect
              className="gx-mb-4"
              placeholder="All user..."
              styles={colourStyles}
              isClearable={true}
              defaultOptions={defaultUserList}
              //value={selectedUser}
              getOptionLabel={(e) => e.label}
              getOptionValue={(e) => e.value}
              loadOptions={loadUserOptions}
              onChange={handleChange}
              maxMenuHeight={175}
            />
          </Col>
        ) : null}
        <Col xxl={6} xl={5} lg={12} md={12} sm={12} xs={12}>
          <AsyncSelect
            placeholder="All domain..."
            className="gx-mb-4"
            styles={colourStyles}
            isClearable={true}
            defaultOptions={defaultDomainList}
            // value={selectedUser}
            getOptionLabel={(e) => e.domain}
            getOptionValue={(e) => e.id}
            loadOptions={loadDomainOptions}
            onChange={handleDomainChange}
            maxMenuHeight={175}
          />
        </Col>
        <Col xxl={12} xl={14} lg={12} md={12} sm={24} xs={24}>
          {!isDatePicker && (
            <span>
              <Select
                menuItemSelectedIcon={<ClockCircleOutlined />}
                defaultValue={timePicker}
                bordered={false}
                style={{
                  borderRadius: "8px",
                  boxShadow: "0 0 5px 5px rgba(0, 0, 0, 0.03)",
                  height: "37px",
                  minWidth: 140,
                  backgroundColor: "white",
                }}
                className="gx-mb-4"
                onChange={handleSelectTimeRange}
              >
                <OptGroup label="Absolute Time">
                  <Option value="datepicker">Date Picker</Option>
                </OptGroup>
                <OptGroup label="Relative Time">
                  <Option value="30m">Past 30m</Option>
                  <Option value="1h">Past 1h</Option>
                  <Option value="6h">Past 6h</Option>
                  <Option value="12h">Past 12h</Option>
                  <Option value="24h">Past 24h</Option>
                  <Option value="7d">Past 7d</Option>
                  <Option value="30d">Past 30d</Option>
                </OptGroup>
              </Select>
              <Select
                menuItemSelectedIcon={<ClockCircleOutlined />}
                defaultValue="paused"
                bordered={false}
                style={{
                  borderRadius: "8px",
                  boxShadow: "0 0 5px 5px rgba(0, 0, 0, 0.03)",
                  height: "37px",
                  minWidth: 140,
                  backgroundColor: "white",
                }}
                className="gx-ml-4 gx-mb-4"
              >
                <OptGroup label="Refresh">
                  <Option value="paused">Paused</Option>
                  <Option value="30s">30s</Option>
                  <Option value="1m">1m</Option>
                  <Option value="3m">3m</Option>
                  <Option value="5m">5m</Option>
                </OptGroup>
              </Select>
            </span>
          )}
          {isDatePicker && (
            <span>
              <RangePicker
                ranges={{
                  Today: [moment(), moment()],
                  "This Month": [
                    moment().startOf("month"),
                    moment().endOf("month"),
                  ],
                }}
                locale={true}
                bordered={true}
                style={{
                  border: "none",
                  // borderRadius: "8px",
                  boxShadow: "0 0 5px 5px rgba(0, 0, 0, 0.03)",
                  height: "37px",
                  minWidth: 140,
                  backgroundColor: "white",
                }}
                showTime
                format="YYYY/MM/DD HH:mm:ss"
                onOk={handleSubmitDatePicker}
              />
              <Button
                style={{
                  margin: "auto",
                  border: "none",
                  borderRadius: "8px",
                  boxShadow: "0 0 5px 5px rgba(0, 0, 0, 0.03)",
                  height: "37px",
                  //   minWidth: 140,
                  // backgroundColor: "red",
                }}
                className="gx-ml-4"
                type="primary"
                onClick={handleCancle}
              >
                <CloseCircleOutlined /> Cancle
              </Button>
            </span>
          )}
        </Col>

        {/* <Col xxl={6} xl={6} lg={12} md={12} sm={12} xs={12}>
          
        </Col> */}
      </Row>
      <Row>
        <Col xl={24} lg={24} md={24} sm={24} xs={24}>
          <CCU CCUData={CCUData} />
          <Traffic
            HttpData={HttpData}
            P2PData={P2PData}
            onFieldChange={onFieldChange}
          />
        </Col>
      </Row>
    </Auxiliary>
  );
};

export default connect(mapStateToProps, {
  getDomainList,
  searchEmail,
  getReportData,
})(Dashboard);
