import React, { useState, useEffect } from "react";
import { Form, Input, message, Col, Row, Select, DatePicker } from "antd";
import { updateTransaction } from "../../../appRedux/actions/Transaction";
import { connect } from "react-redux";
import moment from "moment";

const mapStateToProps = () => ({});

function EditPackage(props) {
  const Option = Select.Option;
  const [transactionInfo, setTransactionInfo] = useState([]);

  const onChange = (newFields) => {
    setTransactionInfo(newFields);
  };

  const handleUpdate = (res) => {
    if (res === 200) {
      message.success("Update transaction  successfully!");
      props.onIsFetch(true);
      props.onClose(false);
    } else {
      message.error("Error when updating transaction !");
      props.onClose(false);
    }
  };

  useEffect(() => {
    setTransactionInfo([
      {
        name: ["paid"],
        value: props.selectedTrans.paid,
      },
      {
        name: ["status"],
        value: props.selectedTrans.status,
      },
      {
        name: ["paydate"],
        value:
          props.selectedTrans && props.selectedTrans.paydate
            ? moment(
                moment(props.selectedTrans.paydate).format("DD-MM-YYYY"),
                "DD/MM/YYYY"
              )
            : null,
      },
    ]);
  }, [props.selectedTrans]);

  useEffect(() => {
    if (props.submitUpdate === true) {
      props.updateTransaction(
        props.selectedTrans.trans_id,
        transactionInfo,
        (res) => handleUpdate(res)
      );
    }
  }, [props.submitUpdate]);

  return (
    <Form
      name="basic"
      className="gx-signin-form gx-form-row0"
      fields={transactionInfo}
      onFieldsChange={(changedFields, allFields) => {
        onChange(allFields);
      }}
    >
      <Row gutter={[16, 0]}>
        <Col span={12}>
          <div className="gx-font-weight-semi-bold">Paid</div>
          <Form.Item
            name="paid"
            rules={[{ required: true, message: "Please input paid money!" }]}
          >
            <Input
              suffix={
                <strong className="gx-text-eco-2">
                  {props.currency ? props.currency : "USD"}
                </strong>
              }
              placeholder="Paid Amount"
              type="number"
            />
          </Form.Item>
        </Col>
        <Col span={12}>
          <div className="gx-font-weight-semi-bold ">Status</div>
          <Form.Item
            name="status"
            rules={[{ required: true, message: "Please choose status " }]}
          >
            <Select>
              <Option value="success">Success</Option>
              <Option value="pending">Pending</Option>
              <Option value="cancle">Cancle</Option>
            </Select>
          </Form.Item>
        </Col>
      </Row>
      <Row gutter={[16, 0]}>
        <Col span={12}>
          <div className="gx-font-weight-semi-bold" style={{ marginBottom: 6 }}>
            Pay date
          </div>
          <Form.Item
            name="paydate"
            rules={[{ required: true, message: "Please input pay date!" }]}
          >
            <DatePicker
              placeholder="Pay Date"
              format="DD/MM/YYYY"
              className="gx-mb-3 gx-w-100"
            />
          </Form.Item>
        </Col>
      </Row>
    </Form>
  );
}

export default connect(mapStateToProps, { updateTransaction })(EditPackage);
