import React, { useState, useEffect } from "react";
import Cookies from "universal-cookie";
import AsyncSelect from "react-select/async";
import axios from "util/Api";
import { Table, Button, Row, Col, Tag, Modal, message, Input } from "antd";
import { PlusCircleOutlined } from "@ant-design/icons";
import CreateTransaction from "./components/CreateTransaction";
import EditTransaction from "./components/EditTransaction";
import { connect } from "react-redux";
import { searchEmail } from "../../appRedux/actions/Dashboard";
import { getListTransaction } from "../../appRedux/actions/Transaction";
import { Pagination } from "antd";
import moment from "moment";

const { Search } = Input;

const mapStateToProps = () => ({});
const TransactionManager = (props) => {
  //#region state
  const [selectedTrans, setSelectedTrans] = useState({});
  const [isFetch, setIsFetch] = useState(false);
  const [keyWord, setKeyWord] = useState("");
  const [isAddTransaction, SetIsAddTransaction] = useState(false);
  const [submitAdd, setSubmitAdd] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [submitUpdate, setSubmitUpdate] = useState(false);
  const [total, setTotal] = useState(50);
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(10);
  const [listData, setListData] = useState([]);

  const [selectedUser, setSelectedUser] = useState(null);
  const [defaultUserList, setDefaultUserList] = useState([]);
  const cookies = new Cookies();
  const roleAccount = cookies.get("role");

  const columns = [
    {
      title: "ID",
      dataIndex: "trans_id",
      key: "trans_id",
    },
    {
      title: "Pack Name",
      dataIndex: "pack_name",
      key: "pack_name",
    },
    {
      title: "Order Code",
      dataIndex: "ordercode",
      key: "ordercode",
    },

    {
      title: "Status",
      dataIndex: "status",
      key: "status",
      render: (status) =>
        status === "success" ? (
          <Tag color="#87d068">Success</Tag>
        ) : status === "pending" ? (
          <Tag color="#483D8B">Pending</Tag>
        ) : (
          <Tag color="#f50">Cancle</Tag>
        ),
    },
    {
      title: "Real Price",
      dataIndex: "real_price",
      key: "real_price",
    },
    {
      title: "Origin Price",
      dataIndex: "origin_price",
      key: "origin_price",
    },
    {
      title: "Paid",
      dataIndex: "paid",
      key: "paid",
    },
    {
      title: "Currency",
      dataIndex: "currency",
      key: "currency",
    },
    {
      title: "Pay date",
      dataIndex: "pay_date",
      key: "pay_date",
      render: (time) => moment(time).utc().format("DD-MM-YYYY HH:mm:ss"),
    },
    {
      title: "Actions",
      key: "action",
      render: (text, record) => (
        <span
          key="btn-verify"
          className="gx-link"
          onClick={() => onEdit(record)}
        >
          <Button
            style={{ width: "80px", height: "35px", margin: "auto" }}
            type="primary"
            disabled={record.status === "success"}
          >
            Edit
          </Button>
          {/* <CheckOutlined /> Verify */}
        </span>
      ),
    },
  ];

  const colourStyles = {
    control: (styles) => ({
      ...styles,
      border: "none",
      borderRadius: "8px",
      boxShadow: "0 0 5px 5px rgba(0, 0, 0, 0.03)",
    }),
    menu: (styles) => ({
      ...styles,
      border: "none",
      borderRadius: "8px",
      boxShadow: "0 0 5px 5px rgba(0, 0, 0, 0.03)",
    }),
  };

  const onIsFetch = (isFetch) => {
    setIsFetch(isFetch);
  };

  const onClose = () => {
    setSubmitAdd(false);
    setSubmitUpdate(false);
    SetIsAddTransaction(false);
    setIsEdit(false);
    setIsFetch(false);
  };

  const onChange = (current, pageSize) => {
    setPage(current);
    setPageSize(pageSize);
  };

  const onEdit = (record) => {
    setIsEdit(true);
    setSelectedTrans(record);
  };

  // Configure react-select
  const loadDefaultUserList = () => {
    if (roleAccount !== "user") {
      props.searchEmail(
        { page: 1, page_size: 20, keyword: "" },
        (code, res) => {
          if (code === 200) {
            console.log(res);
            setDefaultUserList(res);
          } else message.error("Error when fetching user list !");
        }
      );
    }
  };

  const loadUserOptions = (inputValue) => {
    if (roleAccount !== "user") {
      return axios
        .get(`admin/email/search?page=1&page_size=5&keyword=${inputValue}`)
        .then((res) => res.data.data);
    }
  };

  useEffect(() => {
    var email = "";
    if (selectedUser) email = selectedUser.value;
    props.getListTransaction(
      { keyword: keyWord, page: page, page_size: pageSize, email: email },
      (code, res) => handleResult(code, res)
    );
    const handleResult = (code, res) => {
      if (code === 200) {
        // console.log(res);
        setListData(res.data);

        if (!defaultUserList.length) loadDefaultUserList();
        setTotal(res.total);
      } else {
        console.log(code);
        message.error("Error when getting transaction information!");
      }
    };
  }, [page, pageSize, keyWord, selectedUser]);

  useEffect(() => {
    if (isFetch === true) {
      var email = "";
      if (selectedUser) email = selectedUser.value;
      props.getListTransaction(
        { keyword: keyWord, page: page, page_size: pageSize, email: email },
        (code, res) => handleResult(code, res)
      );
      const handleResult = (code, res) => {
        if (code === 200) {
          // console.log(res);
          setListData(res.data);
          setTotal(res.total);
        } else {
          console.log(code);
          message.error("Error when getting transaction information!");
        }
      };
    }
  }, [page, pageSize, isFetch, keyWord, selectedUser]);

  return (
    <div>
      <Row
        // justify="space-between"
        style={{ marginLeft: "-15px", marginRight: "-15px" }}
      >
        <Col xs={24} sm={24} md={24} lg={24} xl={24} xxl={24}>
          <h4
            className="gx-font-weight-semi-bold gx-mb-3"
            style={{ color: "#274679" }}
          >
            TRANSACTION MANAGER
          </h4>
        </Col>
        <Col
          xs={11}
          sm={11}
          md={4}
          lg={4}
          xl={4}
          xxl={3}
          className="gx-order-lg-1 gx-order-md-1"
          style={{ marginRight: "0px" }}
        >
          <Button
            type="default"
            style={{
              // backgroundColor: "#274679",
              fontWeight: "bold",
              height: "37px",
              color: "#274679",
              border: "none",
              borderRadius: "8px",
              boxShadow: "0 0 5px 5px rgba(0, 0, 0, 0.03)",
            }}
            icon={<PlusCircleOutlined />}
            onClick={() => SetIsAddTransaction(true)}
          >
            Add Transaction
          </Button>
        </Col>
        <Col
          xs={13}
          sm={13}
          md={7}
          lg={7}
          xl={6}
          xxl={6}
          className="gx-order-lg-3  gx-order-md-3"
        >
          {roleAccount === "super" || roleAccount === "admin" ? (
            <AsyncSelect
              className="gx-mb-3"
              placeholder="All user..."
              isClearable={true}
              styles={colourStyles}
              defaultOptions={defaultUserList}
              getOptionLabel={(e) => e.label}
              getOptionValue={(e) => e.value}
              loadOptions={loadUserOptions}
              onChange={(value) => setSelectedUser(value)}
              maxMenuHeight={175}
            />
          ) : null}
        </Col>
        <Col
          xs={0}
          sm={0}
          md={1}
          lg={4}
          xl={6}
          xxl={7}
          className="gx-order-lg-2 gx-order-md-2"
        ></Col>
        <Col
          xs={24}
          sm={24}
          md={12}
          lg={8}
          xl={8}
          xxl={8}
          className="gx-order-lg-4 gx-order-md-4"
        >
          <Search
            placeholder="Filter by ordercode ..."
            onSearch={(value) => setKeyWord(value)}
            allowClear
            bordered={false}
            enterButton={<i className="icon i icon-refer" />}
            style={{
              height: "37px",
              backgroundColor: "white",
              borderRadius: "6px",
              boxShadow: "0 0 5px 5px rgba(0, 0, 0, 0.03)",
            }}
          />
        </Col>
      </Row>
      <div
        className="gx-card"
        key="card"
        //  title="DOMAIN CONFIG"
      >
        <Table
          className="gx-table-responsive"
          key="table"
          columns={columns}
          dataSource={listData}
          pagination={false}
          rowKey="id"
          style={{
            border: "none",
            borderRadius: "10px",
            minHeight: "360px",
          }}
        />
        <br />
        <Pagination
          showTotal={(total) => `Total ${total} items`}
          total={total}
          defaultCurrent={page}
          defaultPageSize={pageSize}
          onChange={onChange}
          showQuickJumper
          showSizeChanger
          style={{ marginLeft: "20px" }}
        />

        {isAddTransaction && (
          <Modal
            maskClosable={false}
            title={
              <div className="gx-font-weight-semi-bold gx-text-eco-1">
                ADD NEW TRANSACTION
              </div>
            }
            width={720}
            wrapClassName="vertical-center-modal"
            visible={isAddTransaction}
            onOk={() => setSubmitAdd(true)}
            onCancel={() => SetIsAddTransaction(false)}
            centered={true}
          >
            <CreateTransaction
              submitAdd={submitAdd}
              onClose={onClose}
              onIsFetch={onIsFetch}
            />
          </Modal>
        )}
        {isEdit && (
          <Modal
            maskClosable={false}
            title={
              <div className="gx-font-weight-semi-bold gx-text-eco-1">
                EDIT TRANSACTION
              </div>
            }
            width={720}
            wrapClassName="vertical-center-modal"
            visible={isEdit}
            onOk={() => setSubmitUpdate(true)}
            onCancel={() => setIsEdit(false)}
            centered={true}
          >
            <EditTransaction
              selectedTrans={selectedTrans}
              submitUpdate={submitUpdate}
              onClose={onClose}
              onIsFetch={onIsFetch}
            />
          </Modal>
        )}
      </div>
    </div>
  );
};

export default connect(mapStateToProps, {
  getListTransaction,
  searchEmail,
})(TransactionManager);
