import React, { useState, useEffect } from "react";
import { Col, message, Row } from "antd";
import AsyncSelect from "react-select/async";
// import { DownOutlined } from "@ant-design/icons";
import {
  Area,
  AreaChart,
  Line,
  LineChart,
  ResponsiveContainer,
} from "recharts";

// import { formatDataSize } from "../../../../util/formatData";

import ChartPie from "./components/ChartPie";
import ChartCard from "./components/ChartCard";
import Auxiliary from "util/Auxiliary";

import {
  increamentData,
  increamentRevenueData,
  lineData2,
} from "./components/GraphData/data";
import axios from "util/Api";
import { getDomainList, searchEmail } from "appRedux/actions/Dashboard";
import { getAdvanceReportData } from "appRedux/actions/Report";
import { connect } from "react-redux";
import {
  formatDataSize,
  formatCCU,
  isEmpty,
  formatPercent,
} from "util/formatData";
import Cookies from "universal-cookie";
import jwt_decode from "jwt-decode";
import { set } from "lodash";

const cookies = new Cookies();
const roleAccount = cookies.get("role");
const decoded_token =
  cookies.get("token") != null ? jwt_decode(cookies.get("token")) : null;
const userEmail = decoded_token != null ? decoded_token.email : null;

const mapStateToProps = () => ({});
const Statistic = (props) => {
  const [selectedUser, setSelectedUser] = useState({});
  const [selectedDomain, setSelectedDomain] = useState({});
  const [deviceData, setDeviceData] = useState([
    {
      name: "Unknown",
      value: 1,
    },
  ]);
  const [liveData, setLiveData] = useState([
    {
      name: "Unknown",
      value: 1,
    },
  ]);
  const [ispData, setIspData] = useState([
    {
      name: "Unknown",
      value: 1,
    },
  ]);
  const [domainInfo, setDomainInfo] = useState([]);
  const [defaultUserList, setDefaultUserList] = useState([]);
  const [defaultDomainList, setDefaultDomainList] = useState([]);

  //#region  Xu ly select & search user

  // handle selection
  const handleChange = (value) => {
    setSelectedUser(value);
  };

  const handleDomainChange = (value) => {
    console.log(value);
    setSelectedDomain(value);
  };

  const loadUserOptions = (inputValue) => {
    return axios
      .get(`admin/email/search?page=1&page_size=5&keyword=${inputValue}`)
      .then((res) => res.data.data);
  };

  const loadDomainOptions = (inputValue) => {
    var url = ``;
    var email;
    if (roleAccount === "super" || roleAccount === "admin") {
      email = isEmpty(selectedUser) ? "" : selectedUser.value;
    } else email = userEmail;

    if (isEmpty(selectedUser)) {
      url = `domain/search?page=1&page_size=10&keyword=${inputValue}`;
    } else
      url = `domain/search?email=${email}&page=1&page_size=10&keyword=${inputValue}`;
    return axios.get(url).then((res) => res.data.data.data);
  };

  const getLiveData = () => {
    props.getAdvanceReportData(
      {
        type: "live",
        domain_id: isEmpty(selectedDomain)
          ? cookies.get("statisticDomainID")
          : selectedDomain.id,
      },
      (code, res) => {
        if (code === 200) {
          if (res.total > 0) {
            setLiveData(res.Data);
          } else {
            setLiveData([
              {
                name: "Unknown",
                value: 1,
              },
            ]);
          }
        } else message.error("Error when getting live report data!");
      }
    );
  };

  const getDeviceData = () => {
    props.getAdvanceReportData(
      {
        type: "device",
        domain_id: isEmpty(selectedDomain)
          ? cookies.get("statisticDomainID")
          : selectedDomain.id,
      },
      (code, res) => {
        if (code === 200) {
          if (res.total > 0) {
            setDeviceData(res.Data);
          } else {
            setDeviceData([
              {
                name: "Unknown",
                value: 1,
              },
            ]);
          }
        } else message.error("Error when getting device report data!");
      }
    );
  };

  const getIspData = () => {
    props.getAdvanceReportData(
      {
        type: "isp",
        domain_id: isEmpty(selectedDomain)
          ? cookies.get("statisticDomainID")
          : selectedDomain.id,
      },
      (code, res) => {
        if (code === 200) {
          console.log(res.Data);
          if (res.total > 0) {
            setIspData(res.Data);
          } else {
            setIspData([
              {
                name: "Unknown",
                value: 1,
              },
            ]);
          }
        } else message.error("Error when getting ISP report data!");
      }
    );
  };

  useEffect(() => {
    if (
      (selectedDomain && selectedDomain.id !== undefined) ||
      cookies.get("statisticDomainID")
    ) {
      getDeviceData();
      getIspData();
      getLiveData();
      cookies.set(
        "statisticDomainID",
        isEmpty(selectedDomain) ? "" : selectedDomain.id
      );
    } else {
      setDeviceData([
        {
          name: "Unknown",
          value: 1,
        },
      ]);
      setIspData([
        {
          name: "Unknown",
          value: 1,
        },
      ]);
      setLiveData([
        {
          name: "Unknown",
          value: 1,
        },
      ]);
    }
  }, [selectedDomain]);

  useEffect(() => {
    if (roleAccount === "admin" || roleAccount === "super") {
      props.searchEmail(
        { page: 1, page_size: 20, keyword: "" },
        (code, res) => {
          if (code === 200) {
            setDefaultUserList(res);
          } else message.error("Error when fetching user list !");
        }
      );
    }
  }, []);

  useEffect(() => {
    var email;
    if (roleAccount === "super" || roleAccount === "admin") {
      email = isEmpty(selectedUser) ? "" : selectedUser.value;
    } else email = userEmail;
    props.getDomainList(
      {
        page: 1,
        page_size: 20,
        keyword: "",
        email: email,
      },
      (code, res) => {
        if (code === 200) {
          setDefaultDomainList(res.data);
        } else message.error("Error when fetching domain list !");
      }
    );
  }, [selectedUser]);

  //#endregion

  const colourStyles = {
    control: (styles) => ({
      ...styles,
      border: "none",
      borderRadius: "8px",
      boxShadow: "0 0 5px 5px rgba(0, 0, 0, 0.03)",
    }),
    menu: (styles) => ({
      ...styles,
      border: "none",
      borderRadius: "8px",
      boxShadow: "0 0 5px 5px rgba(0, 0, 0, 0.03)",
    }),
  };

  return (
    <Auxiliary>
      <Row>
        {roleAccount === "admin" || roleAccount === "super" ? (
          <Col xxl={6} xl={6} lg={12} md={12} sm={12} xs={12}>
            <AsyncSelect
              className="gx-mb-4"
              placeholder="All user..."
              styles={colourStyles}
              isClearable={true}
              defaultOptions={defaultUserList}
              //value={selectedUser}
              getOptionLabel={(e) => e.label}
              getOptionValue={(e) => e.value}
              loadOptions={loadUserOptions}
              onChange={handleChange}
              maxMenuHeight={175}
            />
          </Col>
        ) : null}
        <Col xxl={6} xl={6} lg={12} md={12} sm={12} xs={12}>
          <AsyncSelect
            placeholder="All domain..."
            className="gx-mb-4"
            styles={colourStyles}
            isClearable={true}
            defaultOptions={defaultDomainList}
            // value={selectedUser}
            getOptionLabel={(e) => e.domain}
            getOptionValue={(e) => e.id}
            loadOptions={loadDomainOptions}
            onChange={handleDomainChange}
            maxMenuHeight={175}
          />
        </Col>
      </Row>
      <Row>
        <Col xl={6} lg={12} md={12} sm={12} xs={24}>
          <ChartCard
            title={isEmpty(domainInfo) ? 0 : formatCCU(domainInfo.num_rt)} // place data here concurrent user
            children={
              <ResponsiveContainer width="100%" height={75}>
                <AreaChart
                  data={increamentData}
                  margin={{ top: 0, right: 0, left: 0, bottom: 0 }}
                >
                  <defs>
                    <linearGradient id="color1" x1="0" y1="0" x2="1" y2="0">
                      <stop offset="5%" stopColor="#6DD5FA" stopOpacity={0.9} />
                      <stop
                        offset="95%"
                        stopColor="#2980B9"
                        stopOpacity={0.9}
                      />
                    </linearGradient>
                  </defs>
                  <Area
                    dataKey="price"
                    strokeWidth={0}
                    stackId="2"
                    stroke="#867AE5"
                    fill="url(#color1)"
                    fillOpacity={1}
                  />
                </AreaChart>
              </ResponsiveContainer>
            }
            styleName="normal"
            desc="Concurent User"
          />
        </Col>
        <Col xl={6} lg={12} md={12} sm={12} xs={24}>
          <ChartCard
            title={
              isEmpty(domainInfo)
                ? "0 KB"
                : formatDataSize(domainInfo.traffic_p2p_day)
            }
            children={
              <ResponsiveContainer width="100%" height={75}>
                <AreaChart
                  data={increamentRevenueData}
                  margin={{ top: 0, right: 0, left: 0, bottom: 0 }}
                >
                  {/* <Tooltip /> */}
                  <defs>
                    <linearGradient id="color4" x1="0" y1="0" x2="1" y2="0">
                      <stop offset="5%" stopColor="#4ECDE4" stopOpacity={0.9} />
                      <stop
                        offset="95%"
                        stopColor="#06BB8A"
                        stopOpacity={0.9}
                      />
                    </linearGradient>
                  </defs>

                  <Area
                    dataKey="revenue"
                    type="monotone"
                    strokeWidth={0}
                    stackId="2"
                    stroke="#4D95F3"
                    fill="url(#color4)"
                    fillOpacity={1}
                  />
                </AreaChart>
              </ResponsiveContainer>
            }
            //styleName="bac"
            desc="P2P Traffic Today"
          />
        </Col>
        <Col xl={6} lg={12} md={12} sm={12} xs={24}>
          <ChartCard
            title={
              isEmpty(domainInfo)
                ? "0 KB"
                : formatDataSize(domainInfo.traffic_http_day)
            }
            children={
              <ResponsiveContainer width="100%" height={75}>
                <AreaChart
                  data={increamentData}
                  margin={{ top: 0, right: 0, left: 0, bottom: 0 }}
                >
                  {/* <Tooltip /> */}
                  <defs>
                    <linearGradient id="color5" x1="0" y1="0" x2="0" y2="1">
                      <stop offset="5%" stopColor="#e81a24" stopOpacity={0.8} />
                      <stop
                        offset="95%"
                        stopColor="#FEEADA"
                        stopOpacity={0.8}
                      />
                    </linearGradient>
                  </defs>
                  <Area
                    dataKey="price"
                    strokeWidth={0}
                    stackId="2"
                    stroke="#FEEADA"
                    fill="url(#color5)"
                    fillOpacity={1}
                  />
                </AreaChart>
              </ResponsiveContainer>
            }
            //styleName="down"
            desc="HTTP Traffic Today"
          />
        </Col>
        <Col xl={6} lg={12} md={12} sm={12} xs={24}>
          <ChartCard
            title={
              isEmpty(domainInfo)
                ? "0%"
                : formatPercent(
                    domainInfo.traffic_p2p_day,
                    domainInfo.traffic_http_day
                  ) + "%"
            }
            children={
              <ResponsiveContainer width="100%" height={75}>
                <LineChart
                  data={lineData2}
                  margin={{ top: 5, right: 5, left: 5, bottom: 5 }}
                >
                  {/* <Tooltip /> */}
                  <Line
                    dataKey="traffic"
                    stroke="#038FDE"
                    dot={{ stroke: "#FEA931", strokeWidth: 2 }}
                  />
                </LineChart>
              </ResponsiveContainer>
            }
            // styleName="up"
            desc="P2P Offload"
          />
        </Col>
      </Row>
      <Row>
        <Col xl={8} lg={12} md={12} sm={12} xs={24}>
          <ChartPie advanceReportData={deviceData} title="Device" />
        </Col>
        <Col xl={8} lg={12} md={12} sm={12} xs={24}>
          <ChartPie advanceReportData={ispData} title="ISP" />
        </Col>
        <Col xl={8} lg={12} md={12} sm={12} xs={24}>
          <ChartPie advanceReportData={liveData} title="Live" />
        </Col>
      </Row>
    </Auxiliary>
  );
};

export default connect(mapStateToProps, {
  getDomainList,
  searchEmail,
  getAdvanceReportData,
})(Statistic);
