import React, { useState, useEffect } from "react";
import { Form, Input, message, Col, Row, Select } from "antd";
import { DollarCircleOutlined, GlobalOutlined } from "@ant-design/icons";
import { createTransaction } from "../../../appRedux/actions/Transaction";
import { connect } from "react-redux";

const mapStateToProps = () => ({});

function CreateTransaction(props) {
  const Option = Select.Option;
  const [transactionInfo, setTransactionInfo] = useState([]);

  const onChange = (newFields) => {
    setTransactionInfo(newFields);
  };

  const handleAdd = (res) => {
    console.log(res);
    if (res === 200) {
      message.success("Add transaction successfully!");
      props.onIsFetch(true);
    } else message.error("Error when adding transaction !");
    props.onClose();
  };

  useEffect(() => {
    if (props.submitAdd === true) {
      props.createTransaction(transactionInfo, (res) => handleAdd(res));
    }
  }, [props.submitAdd]);

  return (
    <Form
      name="basic"
      className="gx-signin-form gx-form-row0"
      fields={transactionInfo}
      onFieldsChange={(changedFields, allFields) => {
        onChange(allFields);
      }}
    >
      <Row gutter={[16, 0]}>
        <Col span={12}>
          <div className="gx-font-weight-semi-bold" style={{ marginBottom: 6 }}>
            Email
          </div>
          <Form.Item
            name="email"
            rules={[{ required: true, message: "Please input email!" }]}
          >
            <Input placeholder="Email" type="email" />
          </Form.Item>
        </Col>
        <Col span={12}>
          <div className="gx-font-weight-semi-bold gx-mb-1">Package Name</div>
          <Form.Item
            name="pack_name"
            rules={[{ required: true, message: "Please input  package name!" }]}
          >
            <Input placeholder="The display name of package" />
          </Form.Item>
        </Col>
      </Row>
      <Row gutter={[16, 0]}>
        <Col span={12}>
          <div className="gx-font-weight-semi-bold" style={{ marginBottom: 6 }}>
            Transaction Code
          </div>
          <Form.Item
            name="transaction_id"
            rules={[
              { required: true, message: "Please input transaction code!" },
            ]}
          >
            <Input placeholder="Transaction Code" />
          </Form.Item>
        </Col>
        <Col span={12}>
          <div className="gx-font-weight-semi-bold gx-mb-1">Origin Price</div>
          <Form.Item
            name="origin_price"
            rules={[{ required: true, message: "Please input origin price!" }]}
          >
            <Input
              prefix={
                <DollarCircleOutlined style={{ color: "rgba(0,0,0,.25)" }} />
              }
              placeholder="Origin Price"
              type="number"
            />
          </Form.Item>
        </Col>
      </Row>
      <Row gutter={[16, 0]}>
        <Col span={12}>
          <div className="gx-font-weight-semi-bold gx-mb-1">Real Price</div>
          <Form.Item
            name="real_price"
            rules={[{ required: true, message: "Please input real price!" }]}
          >
            <Input
              prefix={
                <DollarCircleOutlined style={{ color: "rgba(0,0,0,.25)" }} />
              }
              placeholder="Real Price"
              type="number"
            />
          </Form.Item>
        </Col>
        <Col span={12}>
          <div className="gx-font-weight-semi-bold" style={{ marginBottom: 6 }}>
            Paid
          </div>
          <Form.Item
            name="paid"
            rules={[{ required: true, message: "Please input paid money!" }]}
          >
            <Input placeholder="Paid Amount" type="number" />
          </Form.Item>
        </Col>
      </Row>
      <Row gutter={[16, 0]}>
        <Col span={12}>
          <div className="gx-font-weight-semi-bold gx-mb-1">Currency</div>
          <Form.Item
            initialValue="USD"
            name="currency"
            rules={[{ required: true, message: "Please input your currency!" }]}
          >
            <Select>
              <Option value="USD">USD</Option>
              <Option value="VND">VND</Option>
            </Select>
          </Form.Item>
        </Col>
        <Col span={12}>
          <div className="gx-font-weight-semi-bold gx-mb-1">Pay ID</div>
          <Form.Item
            initialValue={1}
            name="pay_id"
            rules={[{ required: true, message: "Please choose active or not" }]}
          >
            <Select>
              <Option value={1}>Direct Payment</Option>
              <Option value={2}>Paypal</Option>
            </Select>
          </Form.Item>
        </Col>
      </Row>
      <Row gutter={[16, 0]}>
        <Col span={12}>
          <div className="gx-font-weight-semi-bold gx-mb-1">Status</div>
          <Form.Item
            initialValue="success"
            name="public"
            rules={[{ required: true, message: "Please choose status!" }]}
          >
            <Select>
              <Option value="success">Success</Option>
              <Option value="pending">Pending</Option>
              <Option value="cancle">Cancle</Option>
            </Select>
          </Form.Item>
        </Col>
        <Col span={24}>
          <div className="gx-font-weight-semi-bold gx-mb-1">Description</div>
          <Form.Item
            name="description"
            rules={[{ required: true, message: "Please input description !" }]}
          >
            <Input.TextArea
              prefix={<GlobalOutlined style={{ color: "rgba(0,0,0,.25)" }} />}
              placeholder="Description ..."
            />
          </Form.Item>
        </Col>
      </Row>
    </Form>
  );
}

export default connect(mapStateToProps, { createTransaction })(
  CreateTransaction
);
