import React, { useState, useEffect } from "react";
import Cookies from "universal-cookie";
import { Table, Button, Row, Col, Tag, Modal, message, Input } from "antd";
import { PlusCircleOutlined } from "@ant-design/icons";
import { connect } from "react-redux";
import { searchEmail } from "../../../appRedux/actions/Dashboard";
import { getListTransaction } from "../../../appRedux/actions/Transaction";
import { Pagination } from "antd";
import moment from "moment";
import { useHistory } from "react-router-dom";
import Detail from "./components/Detail";

const { Search } = Input;

const mapStateToProps = () => ({});
const TransactionManager = (props) => {
  const history = useHistory();
  //#region state
  const [keyWord, setKeyWord] = useState("");
  const [total, setTotal] = useState(50);
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(10);
  const [listData, setListData] = useState([]);

  const [isDetail, setIsDetai] = useState(false);
  const [selectedItem, setSelectedItem] = useState({});
  const cookies = new Cookies();
  const email = cookies.get("email");

  const columns = [
    {
      title: "ID",
      dataIndex: "trans_id",
      key: "trans_id",
    },
    {
      title: "Pack Name",
      dataIndex: "pack_name",
      key: "pack_name",
    },
    {
      title: "Order Code",
      dataIndex: "ordercode",
      key: "ordercode",
    },

    {
      title: "Status",
      dataIndex: "status",
      key: "status",
      render: (status) =>
        status === "success" ? (
          <Tag color="#87d068">Success</Tag>
        ) : status === "pending" ? (
          <Tag color="#483D8B">Pending</Tag>
        ) : (
          <Tag color="#f50">Cancle</Tag>
        ),
    },
    {
      title: "Real Price",
      dataIndex: "real_price",
      key: "real_price",
    },
    {
      title: "Origin Price",
      dataIndex: "origin_price",
      key: "origin_price",
    },
    {
      title: "Paid",
      dataIndex: "paid",
      key: "paid",
    },
    {
      title: "Currency",
      dataIndex: "currency",
      key: "currency",
    },
    {
      title: "Pay date",
      dataIndex: "pay_date",
      key: "pay_date",
      render: (time) => moment(time).utc().format("DD-MM-YYYY HH:mm:ss"),
    },
    {
      title: "Actions",
      key: "action",
      render: (text, record) => (
        <span key="btn-verify" className="gx-link">
          <Button
            style={{ width: "80px", height: "35px", margin: "auto" }}
            type="primary"
            onClick={() => onDetail(record)}
          >
            Detail
          </Button>
        </span>
      ),
    },
  ];

  const onDetail = (record) => {
    setIsDetai(true);
    setSelectedItem(record);
  };

  const onChange = (current, pageSize) => {
    setPage(current);
    setPageSize(pageSize);
  };

  useEffect(() => {
    props.getListTransaction(
      { keyword: keyWord, page: page, page_size: pageSize, email: email },
      (code, res) => handleResult(code, res)
    );
    const handleResult = (code, res) => {
      if (code === 200) {
        // console.log(res);
        setListData(res.data);
        setTotal(res.total);
      } else {
        console.log(code);
        message.error("Error when getting transaction information!");
      }
    };
  }, [page, pageSize, keyWord]);

  return (
    <div>
      <Row>
        {" "}
        <Col xs={24} sm={24} md={24} lg={24} xl={24} xxl={24}>
          <h4
            className="gx-font-weight-semi-bold gx-mb-3"
            style={{ color: "#274679", marginLeft: 0 }}
          >
            TRANSACTIONS
          </h4>
        </Col>
      </Row>
      <Row
        justify="space-between"
        style={{ marginLeft: "0px", marginRight: "0px" }}
      >
        <Col style={{ marginRight: "0px" }}>
          <Button
            type="default"
            style={{
              // backgroundColor: "#274679",
              fontWeight: "bold",
              height: "37px",
              color: "#274679",
              border: "none",
              borderRadius: "8px",
              boxShadow: "0 0 5px 5px rgba(0, 0, 0, 0.03)",
            }}
            icon={<PlusCircleOutlined />}
            onClick={() => {
              history.push("resource");
            }}
          >
            Add Transaction
          </Button>
        </Col>

        <Col>
          <Search
            placeholder="Filter by order code ..."
            onSearch={(value) => setKeyWord(value)}
            allowClear
            bordered={false}
            enterButton={<i className="icon i icon-refer" />}
            style={{
              height: "37px",
              backgroundColor: "white",
              borderRadius: "6px",
              boxShadow: "0 0 5px 5px rgba(0, 0, 0, 0.03)",
            }}
          />
        </Col>
      </Row>
      <div className="gx-card" key="card">
        <Table
          className="gx-table-responsive"
          key="table"
          columns={columns}
          dataSource={listData}
          pagination={false}
          rowKey="trans_id"
          style={{
            border: "none",
            borderRadius: "10px",
            minHeight: "360px",
          }}
        />
        <br />
        <Pagination
          showTotal={(total) => `Total ${total} items`}
          total={total}
          defaultCurrent={page}
          defaultPageSize={pageSize}
          onChange={onChange}
          showQuickJumper
          showSizeChanger
          style={{ marginLeft: "20px" }}
        />
        {isDetail && (
          <Modal
            maskClosable={false}
            title={
              <div className="gx-font-weight-semi-bold gx-text-eco-1">
                TRANSACTION DETAIL
              </div>
            }
            width={720}
            wrapClassName="vertical-center-modal"
            visible={isDetail}
            centered={true}
            onCancel={() => setIsDetai(false)}
            onOk={() => setIsDetai(false)}
          >
            <Detail selectedItem={selectedItem} />
          </Modal>
        )}
      </div>
    </div>
  );
};

export default connect(mapStateToProps, {
  getListTransaction,
  searchEmail,
})(TransactionManager);
