import React, { useState, useEffect } from "react";
import Chart from "react-apexcharts";
import { Card } from "antd";
import Widget from "components/Widget";

const ChartPie = (props) => {
  var series = [];
  var label = [];
  var valueData = props.advanceReportData;
  for (let i = 0; i < valueData.length; i++) {
    series.push(valueData[i].value);
  }

  var nameData = props.advanceReportData;
  for (let j = 0; j < valueData.length; j++) {
    label.push(nameData[j].name);
  }
  var options = {
    chart: {
      width: "260",
      type: "donut",
    },
    labels: label,
    dataLabels: {
      enabled: false,
    },
    // responsive: [
    //   {
    //     breakpoint: 480,
    //     options: {
    //       chart: {
    //         width: 200,
    //       },
    //       legend: {
    //         show: false,
    //       },
    //     },
    //   },
    // ],
    legend: {
      show: false,
      // position: "bottom",
      // offsetY: 0,
      // height: 230,
    },
  };

  return (
    <Card
      style={{
        borderRadius: "10px",
        boxShadow: "0 0 5px 5px rgba(0, 0, 0, 0.03)",
        border: "none",
      }}
    >
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <Chart options={options} series={series} type="donut" width={260} />
        <div
          className="gx-font-weight-bold gx-text-eco-1 h4"
          style={{
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
          }}
        >
          {props.title}
        </div>
      </div>
      {/* <h2 className="h4 gx-text-capitalize gx-mb-0">{props.title}</h2> */}
    </Card>
  );
};
export default ChartPie;
