import React, { useRef, useEffect, useState } from "react";
import { message } from "antd";
import { CheckCircleTwoTone } from "@ant-design/icons";
import { createUserDataPack } from "../../../../appRedux/actions/UserDataPack";
import { connect } from "react-redux";
import Cookies from "universal-cookie";

const mapStateToProps = () => ({});

const Paypal = (props) => {
  const cookies = new Cookies();
  const email = cookies.get("email");

  const paypal = useRef();
  const [paid, setPaid] = useState(false);

  const handleAdd = (res) => {
    console.log(res);
    if (res === 200) {
      message.success("Add domain information successfully!");
    } else message.error("Error when adding domain information !");
  };

  useEffect(() => {
    window.paypal
      .Buttons({
        style: {
          color: "silver",
          height: 50,
          label: "pay",
        },
        createOrder: (data, actions, err) => {
          return actions.order.create({
            // intent: "CAPTURE",
            purchase_units: [
              {
                description: props.description,
                amount: {
                  currency_code: props.currency_code
                    ? props.currency_code
                    : "USD",
                  value: Math.round(props.real_price * 11) / 10,
                },
              },
            ],
          });
        },
        onApprove: async (data, actions) => {
          message.loading("Please wait a minute ...");
          const order = await actions.order.capture();
          console.log(order);
          setPaid(true);
          props.createUserDataPack(
            [
              {
                value: props.name,
              },
              {
                value: props.value,
              },
              {
                value: email,
              },
              {
                value: props.expired,
              },
              {
                value: true,
              },
            ],
            (res) => handleAdd(res)
          );
          message.success("Successfully Purchased !");
        },
        onError: (err) => {
          console.log("error ", err);
        },
      })
      .render(paypal.current);
  }, []);

  return (
    <div style={{ width: "80%" }}>
      {paid ? (
        <strong
          className="gx-text-eco-1"
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            fontSize: 16,
          }}
        >
          <CheckCircleTwoTone
            twoToneColor="#52c41a"
            style={{ fontSize: 25, marginRight: 12 }}
          />
          Successfully Purchased !
        </strong>
      ) : (
        // <div style={{ display: "flex", justifyContent: "center" }}>
        //   <CheckCircleTwoTone twoToneColor="#52c41a" style={{ fontSize: 25 }} />
        //   <strong className="gx-ml-3">Successfully Purchased !</strong>
        // </div>
        <div ref={paypal}></div>
      )}
    </div>
  );
};

export default connect(mapStateToProps, {
  createUserDataPack,
})(Paypal);
