import React, { useState } from "react";
import Widget from "components/Widget";
import Chart from "react-apexcharts";
import { Row, Select } from "antd";
import { formatDataSize } from "util/formatData";

const { Option, OptGroup } = Select;

const Traffic = (props) => {
  var categories = []
  var dataHttp = [];
  var httpData = props.HttpData;
  for (let i = 0; i < httpData.length; i++) {
    dataHttp.push(httpData[i].value);
    categories.push(httpData[i].ts)
  }

  var dataP2P = [];
  var p2pData = props.P2PData;
  for (let i = 0; i < p2pData.length; i++) {
    dataP2P.push(p2pData[i].value);
  }



  const options = {
    chart: {
      id: "area-datetime",
      type: "area",
      height: 350,
      zoom: {
        autoScaleYaxis: true,
      },
    },
    // annotations: {
    //   yaxis: [
    //     {
    //       y: 30,
    //       borderColor: "#999",
    //       label: {
    //         show: true,
    //         text: "Support",
    //         style: {
    //           color: "#fff",
    //           background: "#00E396",
    //         },
    //       },
    //     },
    //   ],
    //   xaxis: [
    //     {
    //       x: new Date("14 Nov 2012").getTime(),
    //       borderColor: "#999",
    //       yAxisIndex: 0,
    //       label: {
    //         show: true,
    //         text: "Rally",
    //         style: {
    //           color: "#fff",
    //           background: "#775DD0",
    //         },
    //       },
    //     },
    //   ],
    // },
    dataLabels: {
      enabled: false,
    },
    markers: {
      size: 0,
      style: "hollow",
    },
    yaxis: {
      labels: {
        formatter: function (value) {
          return formatDataSize(value);
        },
      },
    },
    xaxis: {
      type: "datetime",
      labels: {
        datetimeUTC: false,
      },
      // min: new Date("01 Mar 2012").getTime(),
      categories: categories,
      tickAmount: 6,
      tooltip: {
        enabled: false,
      },
    },
    tooltip: {
      x: {
        format: "dd/MM/yyyy HH:mm:ss",
      },
    },
    fill: {
      type: "gradient",
      gradient: {
        shadeIntensity: 1,
        opacityFrom: 0.7,
        opacityTo: 0.9,
        stops: [0, 90, 100],
      },
    },
    stroke: {
      show: true,
      curve: "smooth",
      lineCap: "butt",
      colors: undefined,
      width: 2,
      dashArray: 0,
    },
    chart: {
      animations: {
        enabled: false,
      },
    },
  };

  const [field, setField] = useState("all");

  var series = [];

  const handleSelect = (value) => {
    setField(value);
    props.onFieldChange(value);
  };

  if (field === "all") {
    series = [
      {
        name: "Http Traffic",
        data: dataHttp,
      },
      {
        name: "P2P Traffic",
        data: dataP2P,
      },
    ];
  } else if (field === "http") {
    series = [
      {
        name: "Http Traffic",
        data: dataHttp,
      },
    ];
  } else {
    series = [
      {
        name: "P2P Traffic",
        data: dataP2P,
      },
    ];
  }

  return (
    <Widget>
      <Row justify="space-between" style={{ marginLeft: 0, marginRight: 0 }}>
        <div className="h4 gx-mb-3 gx-font-weight-bold gx-text-eco-1">
          TRAFFIC REPORT
        </div>
        <Select
          // menuItemSelectedIcon={<ClockCircleOutlined />}
          defaultValue="all"
          // bordered={false}
          style={{
            // borderRadius: "8px",
            // boxShadow: "0 0 5px 5px rgba(0, 0, 0, 0.03)",
            height: "37px",
            minWidth: 140,
            // backgroundColor: "white",
          }}
          onChange={handleSelect}
        >
          <OptGroup label="Traffic mode">
            <Option value="all">All</Option>
            <Option value="http">Http Traffic</Option>
            <Option value="p2p">P2P Traffic</Option>
          </OptGroup>
        </Select>
      </Row>
      <hr></hr>
      <Chart options={options} series={series} type="area" height={350} />
    </Widget>
  );
};

export default Traffic;
