import React, { useState, useEffect } from "react";
import { Form, Input, message, Col, Row, Select } from "antd";
import { DollarCircleOutlined, GlobalOutlined } from "@ant-design/icons";
import { createUserDataPack } from "../../../../appRedux/actions/UserDataPack";
import { connect } from "react-redux";

const mapStateToProps = () => ({});

function CreatePackage(props) {
  const Option = Select.Option;
  const [domainInfo, setDomainInfo] = useState([]);
  const [currency, setCurrency] = useState("USD");

  const onChange = (newFields) => {
    console.log(newFields);
    setDomainInfo(newFields);
  };

  const handleAdd = (res) => {
    console.log(res);
    if (res === 200) {
      message.success("Add domain information successfully!");
      props.onIsFetch(true);
    } else message.error("Error when adding domain information !");
    props.onClose();
  };

  useEffect(() => {
    if (props.submitAdd === true) {
      props.createUserDataPack(domainInfo, (res) => handleAdd(res));
    }
  }, [props.submitAdd]);

  return (
    <Form
      name="basic"
      className="gx-signin-form gx-form-row0"
      fields={domainInfo}
      initialValues={{
        active: true,
      }}
      onFieldsChange={(changedFields, allFields) => {
        onChange(allFields);
      }}
    >
      <Row gutter={[16, 0]}>
        <Col span={12}>
          <div className="gx-font-weight-semi-bold gx-mb-1">Package Name</div>
          <Form.Item
            name="pack_name"
            rules={[{ required: true, message: "Please input  package name!" }]}
          >
            <Input placeholder="The display name of package" />
          </Form.Item>
        </Col>
        <Col span={12}>
          <div className="gx-font-weight-semi-bold gx-mb-1">Remain Value</div>
          <Form.Item
            name="remain"
            rules={[{ required: true, message: "Please input remain value!" }]}
          >
            <Input
              type="number"
              suffix={<strong className="gx-text-eco-2">KB</strong>}
              placeholder="The value of package (KB)"
            />
          </Form.Item>
        </Col>
      </Row>

      <Row gutter={[16, 0]}>
        <Col span={12}>
          <div className="gx-font-weight-semi-bold gx-mb-1">Email</div>
          <Form.Item
            name="email"
            rules={[{ required: true, message: "Please input user email!" }]}
          >
            <Input type="email" placeholder="Email" />
          </Form.Item>
        </Col>
        <Col span={12}>
          <div className="gx-font-weight-semi-bold" style={{ marginBottom: 6 }}>
            Expired
          </div>
          <Form.Item
            name="expired"
            rules={[{ required: true, message: "Please input expired date!" }]}
          >
            <Input
              suffix={<strong className="gx-text-eco-2">Months</strong>}
              placeholder="Expired (Months)"
              type="number"
            />
          </Form.Item>
        </Col>
      </Row>
      <Row gutter={[16, 0]}>
        <Col span={12}>
          <div className="gx-font-weight-semi-bold gx-mb-1">Active</div>
          <Form.Item
            name="active"
            rules={[{ required: true, message: "Please choose active or not" }]}
          >
            <Select defaultValue={true}>
              <Option value={true}>Active</Option>
              <Option value={false}>InActive</Option>
            </Select>
          </Form.Item>
        </Col>
      </Row>
    </Form>
  );
}

export default connect(mapStateToProps, { createUserDataPack })(CreatePackage);
