import { FETCH_ERROR, FETCH_START } from "../../constants/ActionTypes";
import qs from "qs";
import axios from "util/Api";

/**
 *  Hm lay thng tin list Domain
 * @param {} page
 * @param {} page_size
 * @param {*} callback
 */
export const getDomainList = (
  { page, page_size, keyword, email },
  callback = (code, response) => {}
) => {
  // console.log(page, page_size);
  return (dispatch) => {
    dispatch({ type: FETCH_START });
    axios
      .get(
        `domain/search?email=${email}&keyword=${keyword}&page=${page}&page_size=${page_size}`
      )
      .then(({ data }) => {
        if (data.code === 200) {
          if (typeof callback === "function") {
            callback(200, data.data);
          }
        } else callback(404);
      })
      .catch(function (error) {
        dispatch({ type: FETCH_ERROR, payload: error });
        console.log("Error****:", error);
        if (typeof callback === "function") {
          callback(404);
        }
      });
  };
};

/**
 *  Hm them domain
 * @param {} domainInfo
 */
export const createDomain = (domainInfo, callback = (code, response) => {}) => {
  return (dispatch) => {
    console.log(domainInfo[0], domainInfo[1]);
    dispatch({ type: FETCH_START });
    axios
      .post(
        `domain`,
        qs.stringify({
          domain: domainInfo[0].value,
          play_url: domainInfo[1].value,
          email: domainInfo[2] ? domainInfo[2].value : "",
        })
      )
      .then(({ data }) => {
        if (data.code === 200) {
          if (typeof callback === "function") {
            callback(200, data);
          }
        } else callback(404);
      })
      .catch(function (error) {
        dispatch({ type: FETCH_ERROR, payload: error });
        console.log("Error****:", error);
        if (typeof callback === "function") {
          callback(404);
        }
      });
  };
};

