import React, { useState, useEffect } from "react";
import Cookies from "universal-cookie";
import AsyncSelect from "react-select/async";
import axios from "util/Api";
import {
  Table,
  Button,
  Row,
  Col,
  Tag,
  Modal,
  message,
  Switch,
  Space,
} from "antd";
import { PlusCircleOutlined } from "@ant-design/icons";
import CreateUserPackage from "./components/CreateUserPackage";
import EditUserPackage from "./components/EditUserPackage";
import { connect } from "react-redux";
import { searchEmail } from "../../../appRedux/actions/Dashboard";
import { getListUserDataPack, updateDataPackStatus } from "../../../appRedux/actions/UserDataPack";
import { Pagination } from "antd";

const mapStateToProps = () => ({});

const TransactionManager = (props) => {
  //#region state
  const [isFetch, setIsFetch] = useState(false);
  const [isAddPackage, setIsAddPackage] = useState(false);
  const [submitAdd, setSubmitAdd] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [total, setTotal] = useState(50);
  const [page, setPage] = useState(1);
  const [userDataPackID, setUserDataPackID] = useState(null);
  const [pageSize, setPageSize] = useState(10);
  const [listData, setListData] = useState([]);
  const [selectedUser, setSelectedUser] = useState(null);
  const [defaultUserList, setDefaultUserList] = useState([]);
  const cookies = new Cookies();
  const roleAccount = cookies.get("role");

  const columns = [
    {
      title: "ID",
      width: "5%",
      render: (text, record) => listData.indexOf(record) + 1,
    },
    {
      title: "Pack Name",
      dataIndex: "pack_name",
      key: "pack_name",
    },
    {
      title: "Value",
      dataIndex: "value",
      key: "value",
    },
    {
      title: "Remain",
      dataIndex: "remain",
      key: "remain",
    },
    {
      title: "Setting",
      key: "active",
      width: "10%",
      render: (text, record) => (
        <Space
          size="large"
        >
          <Switch
            checked={stateSwitch(record)}
            checkedChildren="Active"
            unCheckedChildren="InActive"
            defaultChecked
            onChange={() => handleSwitch(record)}
          />
        </Space>
      ),
    },
    {
      title: "Actions",
      key: "action",
      render: (text, record) => (
        <Space
        size="large"
        onClick={() =>
          handleActionClick(record.userdata_id)
        }
      >
        <span
          key="btn-verify"
          className="gx-link"
          onClick={() => setIsEdit(true)}
        >
          <Button
            style={{ width: "80px", height: "35px", margin: "auto" }}
            type="primary"
          >
            Edit
          </Button>
          {/* <CheckOutlined /> Verify */}
        </span>
        </Space>
      ),
    },
  ];

  const colourStyles = {
    control: (styles) => ({
      ...styles,
      border: "none",
      borderRadius: "8px",
      boxShadow: "0 0 5px 5px rgba(0, 0, 0, 0.03)",
    }),
    menu: (styles) => ({
      ...styles,
      border: "none",
      borderRadius: "8px",
      boxShadow: "0 0 5px 5px rgba(0, 0, 0, 0.03)",
    }),
  };

  const stateSwitch = (record) => {
    if (roleAccount !== "user") {
      return record.active;
    }
  };
  const handleSwitch = (record) => {
    //setTimeout(setIsLoading(true), 200);
    if (roleAccount === "admin" || roleAccount === "super") {
      console.log("admin");
      props.updateDataPackStatus(
        { id: record.userdata_id, active: !record.active },
        (code, res) => {
          if (code === 200) {
            message.success("Set status user package successfully");
            setIsFetch(true);
          } else {
            message.error("Error when verifing domain");
          }
        }
      );
    }
    // setIsLoading(false);
  };

  const handleActionClick = (id) => {
    setUserDataPackID(id);
  };

  const onIsFetch = (isFetch) => {
    setIsFetch(isFetch);
  };

  const onClose = () => {
    setSubmitAdd(false);
    setIsAddPackage(false);
    setIsEdit(false);
    setIsFetch(false);
  };

  const onChange = (current, pageSize) => {
    setPage(current);
    setPageSize(pageSize);
  };

  // Configure react-select
  const loadDefaultUserList = () => {
    if (roleAccount !== "user") {
      props.searchEmail(
        { page: 1, page_size: 20, keyword: "" },
        (code, res) => {
          if (code === 200) {
            console.log(res);
            setDefaultUserList(res);
          } else message.error("Error when fetching user list !");
        }
      );
    }
  };

  const loadUserOptions = (inputValue) => {
    if (roleAccount !== "user") {
      return axios
        .get(`admin/email/search?page=1&page_size=5&keyword=${inputValue}`)
        .then((res) => res.data.data);
    }
  };

  useEffect(() => {
    if (roleAccount === "admin" || roleAccount === "super") {
      loadDefaultUserList();
    }
  }, []);

  useEffect(() => {
    var email = ""
    if (roleAccount === "user") {
      email = cookies.get("email");
    } else {
      if(selectedUser) email = selectedUser.value;
    }
    props.getListUserDataPack(
      { page: page, page_size: pageSize, email: email },
      (code, res) => handleResult(code, res)
    );
    const handleResult = (code, res) => {
      if (code === 200) {
        console.log(res.data);
        setListData(res.data);
        setTotal(res.total)
      } else if (code === 1007){
        setListData([])
        setTotal(0)
      } else {
        console.log(code);
        message.error("Error when getting domain information!");
      }
    };
  }, [page, pageSize, selectedUser]);

  useEffect(() => {
    var email = ""
    if (isFetch === true) {
      if (roleAccount === "user") {
        email = cookies.get("email");
      } else {
        if (selectedUser) email = selectedUser.value;
      }
      props.getListUserDataPack(
        { page: page, page_size: pageSize, email: email },
        (code, res) => handleResult(code, res)
      );
      const handleResult = (code, res) => {
        if (code === 200) {
          // console.log(res);
          setListData(res.data);
          setTotal(res.total);
          setIsFetch(false);
        } else if (code === 1007){
          setListData([])
          setTotal(0)
          setIsFetch(false);
        } else {
          console.log(code);
          message.error("Error when getting domain information!");
        }
      };
    }
  }, [page, pageSize, isFetch, selectedUser]);

  return (
    <div>
      <Row
        justify="space-between"
        style={{ marginLeft: "-15px", marginRight: "-15px" }}
      >
        <Col xs={24} sm={24} md={24} lg={24} xl={24} xxl={24}>
          <h4
            className="gx-font-weight-semi-bold gx-mb-3"
            style={{ color: "#274679" }}
          >
            USER PACKAGE
          </h4>
        </Col>
        <Col
          xs={11}
          sm={11}
          md={4}
          lg={4}
          xl={4}
          xxl={3}
          className="gx-order-lg-1 gx-order-md-1"
          style={{ marginRight: "0px" }}
        >
          <Button
            type="default"
            style={{
              // backgroundColor: "#274679",
              fontWeight: "bold",
              height: "37px",
              color: "#274679",
              border: "none",
              borderRadius: "8px",
              boxShadow: "0 0 5px 5px rgba(0, 0, 0, 0.03)",
            }}
            icon={<PlusCircleOutlined />}
            onClick={() => setIsAddPackage(true)}
          >
            Add Package
          </Button>
        </Col>
        <Col
          xs={13}
          sm={13}
          md={7}
          lg={7}
          xl={6}
          xxl={6}
          className="gx-order-lg-3  gx-order-md-3"
        >
          {roleAccount === "super" || roleAccount === "admin" ? (
            <AsyncSelect
              className="gx-mb-3"
              placeholder="All user..."
              isClearable={true}
              styles={colourStyles}
              defaultOptions={defaultUserList}
              getOptionLabel={(e) => e.label}
              getOptionValue={(e) => e.value}
              loadOptions={loadUserOptions}
              onChange={(value) => setSelectedUser(value)}
              maxMenuHeight={175}
            />
          ) : null}
        </Col>
      </Row>
      <div
        className="gx-card"
        key="card"
        //  title="DOMAIN CONFIG"
      >
        <Table
          className="gx-table-responsive"
          key="table"
          columns={columns}
          dataSource={listData}
          pagination={false}
          rowKey="id"
          style={{
            border: "none",
            borderRadius: "10px",
            minHeight: "360px",
          }}
        />
        <br />
        <Pagination
          showTotal={(total) => `Total ${total} items`}
          total={total}
          defaultCurrent={page}
          defaultPageSize={pageSize}
          onChange={onChange}
          showQuickJumper
          showSizeChanger
          style={{ marginLeft: "20px" }}
        />

        {isAddPackage && (
          <Modal
            maskClosable={false}
            title={
              <div className="gx-font-weight-semi-bold gx-text-eco-1">
                ADD NEW PACKAGE
              </div>
            }
            width={720}
            wrapClassName="vertical-center-modal"
            visible={isAddPackage}
            onOk={() => setSubmitAdd(true)}
            onCancel={() => setIsAddPackage(false)}
            centered={true}
          >
            <CreateUserPackage
              submitAdd={submitAdd}
              onClose={onClose}
              onIsFetch={onIsFetch}
            />
          </Modal>
        )}
        {isEdit && (
          <Modal
            maskClosable={false}
            title={
              <div className="gx-font-weight-semi-bold gx-text-eco-1">
                EDIT USER PACKAGE
              </div>
            }
            width={450}
            wrapClassName="vertical-center-modal"
            visible={isEdit}
            onOk={() => setSubmitAdd(true)}
            onCancel={() => setIsEdit(false)}
            centered={true}
          >
            <EditUserPackage
              userDataPackID={userDataPackID}
              submitAdd={submitAdd}
              onClose={onClose}
              onIsFetch={onIsFetch}
            />
          </Modal>
        )}
      </div>
    </div>
  );
};

export default connect(mapStateToProps, {
  getListUserDataPack,
  updateDataPackStatus,
  searchEmail,
})(TransactionManager);
