import React, { useState, useEffect } from "react";
import { Form, Input, message, Col, Row, Select } from "antd";
import { GlobalOutlined } from "@ant-design/icons";
import { createDomain } from "../../../appRedux/actions/DomainConfig";
import { updateDataPack } from "../../../appRedux/actions/DataPack"
import { connect } from "react-redux";

const mapStateToProps = () => ({});

function EditPackage(props) {
  const Option = Select.Option;
  const [domainInfo, setDomainInfo] = useState([]);

  const onChange = (newFields) => {
    console.log(newFields);
    setDomainInfo(newFields);
  };

  const handleAdd = (res) => {
    console.log(res);
    if (res === 200) {
      message.success("Add domain information successfully!");
      props.onIsFetch(true);
    } else message.error("Error when adding domain information !");
    props.onClose();
  };

  useEffect(() => {
    if (props.submitAdd === true) {
      props.updateDataPack(domainInfo, props.packageInfo.id ,(res) => handleAdd(res));
    }
  }, [props.submitAdd]);

  return (
    <Form
      name="basic"
      className="gx-signin-form gx-form-row0"
      fields={domainInfo}
      initialValues={props.packageInfo}
      onFieldsChange={(changedFields, allFields) => {
        onChange(allFields);
      }}
    >
      <Row gutter={[16, 0]}>
        <Col span={12}>
          <div className="gx-font-weight-semi-bold gx-mb-1">Active</div>
          <Form.Item
            name="active"
            rules={[{ required: true, message: "Please choose active or not" }]}
          >
            <Select>
              <Option value={true}>Active</Option>
              <Option value={false}>InActive</Option>
            </Select>
          </Form.Item>
        </Col>
        <Col span={12}>
          <div className="gx-font-weight-semi-bold gx-mb-1">Public</div>
          <Form.Item
            name="public"
            rules={[
              { required: true, message: "Please choose public or not!" },
            ]}
          >
            <Select>
              <Option value={true}>Public</Option>
              <Option value={false}>Not Public</Option>
            </Select>
          </Form.Item>
        </Col>
      </Row>
      <Row gutter={[16, 0]}>
        <Col span={12}>
          <div className="gx-font-weight-semi-bold" style={{ marginBottom: 6 }}>
            Expired
          </div>
          <Form.Item
            name="expired"
            rules={[{ required: true, message: "Please input expired date!" }]}
          >
            <Input
              suffix={<strong className="gx-text-eco-2">Months</strong>}
              placeholder="Expired (Months)"
              type="number"
            />
          </Form.Item>
        </Col>
        <Col span={24}>
          <div className="gx-font-weight-semi-bold gx-mb-1">Description</div>
          <Form.Item
            name="description"
            rules={[{ required: true, message: "Please input description !" }]}
          >
            <Input.TextArea
              style={{ minHeight: 34 }}
              prefix={<GlobalOutlined style={{ color: "rgba(0,0,0,.25)" }} />}
              placeholder="Description ..."
            />
          </Form.Item>
        </Col>
      </Row>
    </Form>
  );
}

export default connect(mapStateToProps, { updateDataPack })(EditPackage);
