import React, { useState, useEffect } from "react";
import { Form, Input, message, Col, Row, Select } from "antd";
import { createDomain } from "../../../../appRedux/actions/DomainConfig";
import { updateDataPackRemain } from "../../../../appRedux/actions/UserDataPack";
import { connect } from "react-redux";

const mapStateToProps = () => ({});

function EditPackage(props) {
  const Option = Select.Option;
  const [domainInfo, setDomainInfo] = useState([]);

  const onChange = (newFields) => {
    console.log(newFields);
    setDomainInfo(newFields);
  };

  const handleAdd = (res) => {
    console.log(res);
    if (res === 200) {
      message.success("Add domain information successfully!");
      props.onIsFetch(true);
    } else message.error("Error when adding domain information !");
    props.onClose();
  };

  useEffect(() => {
    if (props.submitAdd === true) {
      props.updateDataPackRemain(domainInfo, props.userDataPackID, (res) => handleAdd(res));
    }
  }, [props.submitAdd]);

  return (
    <Form
      name="basic"
      className="gx-signin-form gx-form-row0"
      fields={domainInfo}
      onFieldsChange={(changedFields, allFields) => {
        onChange(allFields);
      }}
    >
      <Row gutter={[16, 0]}>
        <Col span={24}>
          <div className="gx-font-weight-semi-bold">Remain</div>
          <Form.Item
            name="remain"
            rules={[{ required: true, message: "Please input remain data!" }]}
          >
            <Input
              suffix={<strong className="gx-text-eco-2">KB</strong>}
              placeholder="Remain Data (KB)"
              type="number"
            />
          </Form.Item>
        </Col>
      </Row>
    </Form>
  );
}

export default connect(mapStateToProps, { updateDataPackRemain })(EditPackage);
