import { FETCH_ERROR, FETCH_START } from "../../constants/ActionTypes";
import qs from "qs";
import axios from "util/Api";

/**
 *  Hm lay thng tin DataPack
 * @param {} id
 */
export const getUserDataPackDetail = (
    { id },
    callback = (code, response) => {}
  ) => {
    // console.log(page, page_size);
    return (dispatch) => {
      dispatch({ type: FETCH_START });
      axios
        .get(
          `userpacks/information?id=${id}`
        )
        .then(({ data }) => {
          if (data.code === 200) {
            if (typeof callback === "function") {
              callback(200, data.data);
            }
          } else callback(404);
        })
        .catch(function (error) {
          dispatch({ type: FETCH_ERROR, payload: error });
          console.log("Error****:", error);
          if (typeof callback === "function") {
            callback(404);
          }
        });
    };
  };

/**
 *  Hm lay thng tin list Datapack
 * @param {} page
 * @param {} page_size
 * @param {*} callback
 */
export const getListUserDataPack = (
    { page, page_size, email },
    callback = (code, response) => {}
  ) => {
    // console.log(page, page_size);
    return (dispatch) => {
      dispatch({ type: FETCH_START });
      axios
        .get(
          `userpacks?email=${email}&page=${page}&page_size=${page_size}`
        )
        .then(({ data }) => {
          if (data.code === 200) {
            if (typeof callback === "function") {
              callback(data.code, data.data);
            }
          } else callback(data.code);
        })
        .catch(function (error) {
          dispatch({ type: FETCH_ERROR, payload: error });
          console.log("Error****:", error);
          if (typeof callback === "function") {
            callback(404);
          }
        });
    };
};

/**
 *  Hm them datapack
 * @param {} userDataPackInfo
 */
export const createUserDataPack = (userDataPackInfo,callback = (code, response) => {}) => {
    return (dispatch) => {
      console.log(userDataPackInfo);
      dispatch({ type: FETCH_START });
      axios
        .post(
          `userpacks`,
          qs.stringify({
            pack_name: userDataPackInfo[0].value,
            remain: userDataPackInfo[1].value,
            email: userDataPackInfo[2].value,
            expired: userDataPackInfo[3].value,
            active: userDataPackInfo[4].value
          })
        )
        .then(({ data }) => {
          if (data.code === 200) {
            if (typeof callback === "function") {
                callback(data.code);
              }
            } else callback(data.code, null);
        })
        .catch(function (error) {
          dispatch({ type: FETCH_ERROR, payload: error });
          console.log("Error****:", error);
          if (typeof callback === "function") {
            callback(404, null);
          }
        });
    };
};

/**
 *  Hm delete datapack
 * @param {} dataPackID
 */

export const deleteDatapack = (userDataPackID, callback = (code, response) => {}) => {
    // console.log(page, page_size);
    return (dispatch) => {
      dispatch({ type: FETCH_START });
      axios
        .delete(`userpacks/${userDataPackID}`)
        .then(({ data }) => {
          if (data.code === 200) {
            if (typeof callback === "function") {
              callback(200, data.data);
            }
          } else callback(404);
        })
        .catch(function (error) {
          dispatch({ type: FETCH_ERROR, payload: error });
          console.log("Error****:", error);
          if (typeof callback === "function") {
            callback(404);
          }
        });
    };
};


/**
 *  Hm update thng tin data Pack (admin  mi c quyn)
 * @param {userDataPackInfo} 
 */
export const updateDataPackRemain = (userDataPackInfo, userDataPackID, callback = (response) => {}) => {
    return (dispatch) => {
      axios
        .put(
          "userpacks/remain",
          qs.stringify({
            id: userDataPackID,
            remain: userDataPackInfo[0].value
          })
        )
        .then((res) => {
          if (res.data.code === 200) {
            console.log("update datapack: ", res);
            if (typeof callback === "function") {
              callback(res.data.code);
            }
          } else {
            callback(res.data.code);
          }
        })
        .catch(function (error) {
          dispatch({ type: FETCH_ERROR, payload: error });
          console.log("Error****:", error);
          callback(404);
        });
    };
};

